.header {
    color: white;
    display: flex;
    padding:20px;
    align-items: center;
    justify-content: space-between;
    overflow-x: scroll;
}

/* Hide scrollbar for Chrome, Safari and Opera */
.header::-webkit-scrollbar {
    display: none;
  }
  
  /* Hide scrollbar for IE, Edge and Firefox */
  .header {
    -ms-overflow-style: none;  /* IE and Edge */
    scrollbar-width: none;  /* Firefox */
  }

.header__icons {
    display: flex;
    align-items: center;
    text-align: center;

}

.header__icon > .MuiSvgIcon-root {
    font-size: 30px;
    margin-left: auto;
    margin-right: auto;
}

.header__icon--active > p {
   display: inline-flex !important; 
}

.header__icon {
    display: flex;
    flex-direction: column;
    width: 100px;
    cursor: pointer;
    
}

.header__icon:hover > p {
    display: inline-flex;
}

.header__icon > p {
    display: none;
    letter-spacing:3px;
    font-size: small;
    margin-left: auto;
    margin-right: auto;
}


.header > img {
    object-fit: contain;
    height: 120px;
    padding-right:40px;
}